<template>
<div class="mainWrapper">
    <el-card class="fgt_card">
        <div class="fgt_logo">
            <el-image src="#" fit="fill" :lazy="true">
                <div slot="error" class="image-slot">
                    <i class="el-icon-picture-outline"></i>
                </div>
            </el-image>
        </div>
        <div class="fgt_content">
            <div class="fgt_title">
                <p>Forget Password</p>
            </div>
            <div class="fgt_steps">
                <el-steps :active="stepActive" process-status="error">
                    <el-step></el-step>
                    <el-step></el-step>
                    <el-step></el-step>
                </el-steps>
                <div class="fgt_inputs">
                    <div class="first_step" v-if="componentShow.first">
                        <el-form :model="firstForm" ref="firstForm" :rules="firstRules" size="normal">
                            <el-form-item prop="email">
                                <el-input v-model="firstForm.email" placeholder="E-mail"></el-input>
                            </el-form-item>
                            <el-form-item prop="code">
                                <el-row :gutter="20">
                                    <el-col :span="12" :offset="0">
                                        <el-input v-model="firstForm.code" placeholder="verification code"></el-input>
                                    </el-col>
                                    <el-col :span="12" :offset="0">
                                        <el-button type="primary" size="default" @click="getCode">Get verfication code</el-button>
                                    </el-col>
                                </el-row>
                            </el-form-item>
                            <el-button style="width: 100%" type="primary" size="default" @click="firstStep">Next step</el-button>
                            <el-form-item></el-form-item>
                        </el-form>
                    </div>
                    <div class="second_step" v-if="componentShow.second">
                        <el-form :model="secondForm" ref="secondForm" :rules="secondRules" size="normal">
                            <el-form-item prop="psw">
                                <el-input v-model="secondForm.psw" placeholder="Reset password"></el-input>
                            </el-form-item>
                            <el-form-item prop="rePsw">
                                <el-input v-model="secondForm.rePsw" placeholder="Confrim password"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" style="width: 100%" @click="secondStep">Next step</el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                    <div class="third_step" v-if="componentShow.third">
                        <i class="el-icon-circle-check"></i>
                        <p class="content">
                            You have successfully changed your password.
                        </p>
                        <p class="content">
                            please log in with the new password
                        </p>
                        <el-button style="width: 100%" type="primary" size="default" @click="login">Log in now</el-button>
                    </div>
                </div>
            </div>
        </div>
    </el-card>
</div>
</template>

<script>
import {
    vertify,
    firstStep,
    secondStep
} from '@/api/mockAPI.js';
export default {
    data() {
        return {
            componentShow: {
                first: true,
                second: false,
                third: false,
            },
            stepActive: 1,
            firstForm: {
                email: 'mkd@163.com',
                code: '123456',
            },
            secondForm: {
                psw: '123456',
                rePsw: '123456',
            },
            firstRules: {
                email: [{
                    required: true,
                    trigger: 'blur',
                }, ],
                code: [{
                    required: true,
                    trigger: 'blur',
                }, ],
            },
            secondRules: {
                psw: {
                    requied: true,
                    trigger: 'blur',
                },
                rePsw: [{
                        required: true,
                        trigger: 'blur',
                    },
                    {
                        validator: (rule, value, callback) => {
                            if (this.secondForm.psw !== this.secondForm.rePsw) {
                                callback(new Error('Make sure the enter is same!'));
                            }
                            callback();
                        },
                    },
                ],
            },
        };
    },
    methods: {
        getCode() {
            // 邮箱是存在的
            if (this.firstForm.email) {
                vertify({
                    email: this.firstForm.email,
                }).then((res) => {
                    if (res.data.code === 0) {
                        this.$message({
                            message: 'the verification code have send to your email, please check!',
                            type: 'success',
                        });
                    }
                });
            } else {
                this.$message.error('please enter the email');
            }
        },
        login() {
            this.$router.push('/');
        },
        firstStep() {
            this.$refs.firstForm.validate((valid) => {
                if (valid) {
                    firstStep(this.firstForm).then((res) => {
                        if (res.data.code === 0) {
                            this.componentShow.first = false;
                            this.componentShow.second = true;
                        }
                    });
                }
            });
        },
        secondStep() {
            this.$refs.secondForm.validate((valid) => {
                if (valid) {
                    secondStep(this.secondForm).then((res) => {
                        if (res.data.code === 0) {
                            this.componentShow.second = false;
                            this.componentShow.third = true;
                        }
                    });
                }
            });
        },
    },
};
</script>

