    max-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba($color: $--color-primary, $alpha: 0.1);

    .fgt_card {
        width: 50%;
    }
}

.fgt_content {
    .fgt_title {
        text-align: center;
        background-color: $--color-primary;
        padding: 1.5em 0;
        border-radius: 10px 10px 0px 0px;
    }

    .fgt_steps {
        margin: 3em 8em 0;

        .fgt_inputs {
            margin: 2em 2em;
        }
    }
}

.third_step {
    text-align: center;

    .content {
        padding: 1em 0;
    }
}
</style>
